form{
    display:flex;
    padding:10px;
    background-color: #333;
}

input[type="text"]{
    flex:1;
    margin-right: 10px;
    border: none;
    outline:none;
    padding:7px;

}
input[type="submit"]{
    flex:0.23;
    background:rgba(20,100,20,1);
    color:white;
    border:none;
    outline:none;
    cursor:pointer
}
input[type="submit"]:hover{
    background-color: rgba(30,300,20,0.5);
}
