header{
    background-color: #333;
    color:white;
    padding:10px;
    text-align: center;
    font-family: sans-serif;
    font-weight: 200;
    position:sticky;
    top:0;
}
header h1{
    margin-bottom: 10px;
}
header a {
    color:white;
    text-decoration: none;
}
header a:hover{
    color:red;
}